import MyVerticallyCenteredModal from 'components/Modal/TradeStockModal';
import { useGetUserStockListQuery } from 'quires/useUserQuery';
import React, { useState } from 'react';
import styled from 'styled-components';

// react-bootstrap components
import { Button, Card, Table, Container, Row, Col, ProgressBar } from 'react-bootstrap';
import { getStockName } from 'util/stock';
import { setComma } from 'util/util';

const Total = styled.td`
	font-weight: bold;
`;

const getNumberColor = num => {
	return num < 0 ? { color: 'blue' } : { color: 'red' };
};

const getProgressColor = num => {
	switch (Math.floor(num / 33)) {
		case 0:
			return 'warning';
		case 1:
			return 'success';
		default:
			return 'danger';
	}
};

function TableList() {
	const [modalShow, setModalShow] = useState(false);
	const [modalStockInfo, setModalStockInfo] = useState({});

	const { data, refetch: stockRefetch, status } = useGetUserStockListQuery();
	const stockList = data?.stockList ?? [];
	const totalStockValue = data?.totalStockValue ?? 0;
	const totalMyValue = data?.totalMyValue ?? 0;
	const totalProfitValue = totalStockValue - totalMyValue;
	const totalMyMoney = data?.totalMyMoney ?? 0;
	return (
		<>
			<Container fluid>
				<MyVerticallyCenteredModal
					show={modalShow}
					onHide={() => {
						setModalShow(false);
					}}
					myStockInfo={modalStockInfo}
					totalMyMoney={totalMyMoney}
					dataRefresh={stockRefetch}
				/>
				<Row>
					<Col md="12">
						<Card className="strpied-tabled-with-hover">
							<Card.Header>
								<Card.Title as="h4">
									 {' '}
									<i
										style={{ cursor: 'pointer' }}
										onClick={stockRefetch}
										className={`nc-icon nc-refresh-02 ${
											status !== 'success' && 'fa-spin'
										}`}
									></i>
								</Card.Title>
								<p className="card-category"> 3%  .</p>
							</Card.Header>
							<Card.Body className="table-full-width table-responsive px-0">
								<Table className="table-hover table-striped">
									<thead>
										<tr>
											<th className="border-0">ID</th>
											<th className="border-0"></th>
											<th className="border-0"> </th>
											<th className="border-0"> </th>
											<th className="border-0"> </th>
											<th className="border-0"> </th>
											<th className="border-0"> </th>
											<th className="border-0"> </th>
											<th className="border-0">/</th>
										</tr>
									</thead>
									{stockList.map((myStock, key) => {
										return (
											<tbody key={key}>
												<tr>
													<td>{key + 1}</td>
													<td>
														[{getStockName(myStock.stockType)}]{myStock.name}
													</td>
													<td>
														{setComma(myStock.myValue, true)}/
														<span style={getNumberColor(myStock.myRatio)}>
															{setComma(myStock.myRatio)}%
														</span>
													</td>
													<td>{setComma(myStock.stockValue, true)}</td>
													<td style={getNumberColor(myStock.profilMargin)}>
														{setComma(myStock.profilMargin, true)}
													</td>
													<td>{setComma(myStock.cnt * myStock.myValue, true)}</td>
													<td>{setComma(myStock.cnt * myStock.stockValue, true)}</td>
													<td>
														<ProgressBar
															striped
															variant={getProgressColor(myStock.holdingRatio)}
															label={`${myStock.holdingRatio}%`}
															now={myStock.holdingRatio}
														/>
													</td>
													<td>
														<Button
															onClick={() => {
																setModalStockInfo(myStock);
																setModalShow(true);
															}}
															variant="danger"
															size="sm"
														>
															/
														</Button>
													</td>
												</tr>
											</tbody>
										);
									})}
									<tbody>
										<tr>
											<Total></Total>
											<td></td>
											<td></td>
											<td></td>
											<Total style={getNumberColor(totalProfitValue)}>
												{setComma(totalProfitValue, true)}
											</Total>
											<Total>{setComma(totalMyValue, true)}</Total>
											<Total>{setComma(totalStockValue, true)}</Total>
											<td></td>
											<td></td>
										</tr>
									</tbody>
								</Table>
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</Container>
		</>
	);
}

export default TableList;
