import axios from 'axios';
import MyVerticallyCenteredModal from 'components/Modal/GiveMoneyModal';
import { useGetUserInfoQuery } from 'quires/useUserQuery';
import PropTypes from 'prop-types';
import React, { useRef, useState } from 'react';

// react-bootstrap components
import { Button, Card, Form, Container, Row, Col } from 'react-bootstrap';
import { setComma } from 'util/util';

function GrantMoneyLabel({ isLoadingInfo, isFail }) {
	if (isFail) {
		return (
			<label>
				<i className="nc-icon nc-simple-remove"></i>
			</label>
		);
	}
	if (isLoadingInfo) {
		return (
			<label>
				<i className="nc-icon nc-refresh-02 fa-spin"></i>
			</label>
		);
	}
	return <label>-</label>;
}

function User() {
	const [modalShow, setModalShow] = useState(false);
	const [isLoadingInfo, setLoadingInfo] = useState(false);
	const [isFail, setIsFail] = useState(false);
	const myMoneyRef = useRef(null);
	const { data, refetch: userRefetch } = useGetUserInfoQuery();

	const getGrantMoney = async () => {
		try {
			setIsFail(false);
			setLoadingInfo(true);
			await axios.patch(`${process.env.REACT_APP_BACK_API}/user/grantmoney`, null, {
				withCredentials: true,
			});

			userRefetch();
		} catch (e) {
			setIsFail(true);
		} finally {
			setLoadingInfo(false);
		}
	};

	const nickname = data?.nickname ?? '';
	const totalStockValue = data?.totalStockValue ?? 0;
	const grantMoney = data?.grantMoney ?? 0;
	const myMoney = data?.myMoney ?? 0;
	return (
		<>
			<Container fluid>
				<MyVerticallyCenteredModal
					show={modalShow}
					onHide={() => {
						setModalShow(false);
					}}
					myNickname={nickname}
					totalMyMoney={myMoney}
					dataRefresh={userRefetch}
				/>
				<Row>
					<Col md="8">
						<Card>
							<Card.Header>
								<Card.Title as="h4"> </Card.Title>
							</Card.Header>
							<Card.Body>
								<Form>
									<Row>
										<Col className="pr-1" md="3">
											<Form.Group>
												<label></label>
												<Form.Control
													defaultValue="Discord "
													disabled
													placeholder="Company"
													type="text"
												></Form.Control>
											</Form.Group>
										</Col>
										<Col className="px-1" md="9">
											<Form.Group>
												<label></label>
												<Form.Control
													defaultValue={nickname}
													placeholder=""
													type="text"
													disabled
												></Form.Control>
											</Form.Group>
										</Col>
									</Row>
									<Row>
										<Col className="pr-1" md="6">
											<Form.Group>
												<label> </label>
												<Form.Control
													ref={myMoneyRef}
													value={`${setComma(myMoney, true)}`}
													placeholder=" "
													type="text"
													disabled
												></Form.Control>
											</Form.Group>
										</Col>
										<Col className="px-1" md="6">
											<Form.Group>
												<label>
													 {' - '}
													<small
														style={{ cursor: 'pointer' }}
														className="text-danger"
														onClick={() => {
															location.href = '/admin/stock';
														}}
													>
														
													</small>
												</label>
												<Form.Control
													value={`${setComma(totalStockValue, true)}`}
													placeholder=" "
													type="text"
													disabled
												></Form.Control>
											</Form.Group>
										</Col>
									</Row>
									<Row>
										<Col className="pr-1" md="6">
											<Form.Group>
												<label> ()</label>
												<Form.Control
													value={`${setComma(grantMoney, true)}`}
													placeholder=" "
													type="text"
													disabled
												></Form.Control>
											</Form.Group>
										</Col>
										<Col className="px-1" md="3">
											<Form.Group>
												<GrantMoneyLabel
													isLoadingInfo={isLoadingInfo}
													isFail={isFail}
												></GrantMoneyLabel>
												<Form.Control
													className="btn-primary"
													defaultValue=" "
													placeholder=""
													type="button"
													onClick={getGrantMoney}
												></Form.Control>
											</Form.Group>
										</Col>
									</Row>
									<hr />
									<Button
										className="btn-fill pull-right"
										variant="primary"
										onClick={() => {
											setModalShow(true);
										}}
									>
										 
									</Button>{' '}
									<Button
										className="btn-fill pull-right"
										variant="info"
										onClick={() => {
											location.href = '/admin/stock';
										}}
									>
										
									</Button>{' '}
									<Button className="btn-fill pull-right" variant="info">
										
									</Button>{' '}
									<Button className="btn-fill pull-right" variant="danger">
										
									</Button>
									<div className="clearfix"></div>
								</Form>
							</Card.Body>
						</Card>
					</Col>
					<Col md="4">
						<Card className="card-user">
							<div className="card-image">
								<img alt="..." src={require('assets/img/profileBackground.jpg')}></img>
							</div>
							<Card.Body>
								<div className="author">
									<a href="#pablo" onClick={e => e.preventDefault()}>
										<img
											className="avatar border-gray"
											src={require('assets/img/faces/profileImg2.jpeg')}
										></img>
										<h5 className="title">{nickname}</h5>
									</a>
									<p className="description"> ~</p>
								</div>
								<p className="description text-center">!</p>
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</Container>
		</>
	);
}

GrantMoneyLabel.propTypes = {
	isLoadingInfo: PropTypes.bool.isRequired,
	isFail: PropTypes.bool.isRequired,
};

export default User;
