import React, { useEffect, useState, useRef } from 'react';
import { Modal, Button, Form, Container, Row, Col, Table } from 'react-bootstrap';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import NotificationAlert from 'react-notification-alert';

import axios from 'axios';
import { getStockName } from 'util/stock';
import { setComma } from '../../util/util';

const getNumberColor = num => {
	return num < 0 ? { color: 'blue' } : { color: 'red' };
};

function MyVerticallyCenteredModal({
	onHide,
	show,
	myStockInfo,
	dataRefresh,
	totalMyMoney,
}) {
	const {
		register,
		handleSubmit,
		formState: { errors },
		getValues,
		setValue,
	} = useForm();
	const [totalMoney, setTotalMoney] = useState(0);
	const [stockInfo, setStockInfo] = useState({});
	const [isLoadingInfo, setLoadingInfo] = useState(false);
	const myValueRef = useRef(null);
	const myCntRef = useRef(null);
	const notificationAlertRef = useRef(null);
	const notify = (colorType, message) => {
		let type;
		switch (colorType) {
			case 1:
				type = 'primary';
				break;
			case 2:
				type = 'success';
				break;
			case 3:
				type = 'danger';
				break;
			case 4:
				type = 'warning';
				break;
			case 5:
				type = 'info';
				break;
			default:
				break;
		}
		const options = {
			place: 'tr',
			message: (
				<div>
					<div>{message}</div>
				</div>
			),
			type,
			icon: 'nc-icon nc-bell-55',
			autoDismiss: 7,
		};
		notificationAlertRef.current.notificationAlert(options);
	};

	const initFormData = () => {
		setValue('type', 'b');
		setValue('cnt', 1);
		setTotalMoney(Math.floor(stockInfo?.value));
	};

	const fetchData = async name => {
		if (isLoadingInfo) {
			return;
		}
		setLoadingInfo(true);
		const result = await axios.get(`${process.env.REACT_APP_BACK_API}/stock`, {
			withCredentials: true,
			params: { name },
		});
		setLoadingInfo(false);
		setStockInfo(result.data);
		setTotalMoney(Math.floor(result?.data?.value ?? 0));
	};

	useEffect(() => {
		if (myStockInfo?.name) {
			fetchData(myStockInfo.name);
		}
	}, [myStockInfo]);
	const onSubmit = async data => {
		try {
			setLoadingInfo(true);
			const { data: result } = await axios.patch(
				`${process.env.REACT_APP_BACK_API}/user/stock`,
				{ ...data, stockName: myStockInfo.name },
				{
					withCredentials: true,
				},
			);
			notify(1, ' !');

			myCntRef.current.innerText = `${setComma(result.cnt)}`;
			myValueRef.current.innerText = `${setComma(result.value)}`;
			initFormData();
			dataRefresh();
		} catch (e) {
			let message = '...';
			if (e.response) {
				message = e.response?.data?.message ?? '...';
			}
			notify(3, message);
		} finally {
			setLoadingInfo(false);
		}
	};
	const calcTotalMoney = () => {
		const type = getValues('type');
		const cnt = getValues('cnt');

		setTotalMoney(Math.floor(cnt * (stockInfo?.value ?? 0) * (type === 's' ? 0.98 : 1)));
	};

	const onCloseModal = () => {
		initFormData();
		onHide();
	};

	return (
		<Modal
			show={show && myStockInfo?.name}
			onHide={onCloseModal}
			size="lg"
			aria-labelledby="example-modal-sizes-title-lg"
		>
			<NotificationAlert ref={notificationAlertRef} />
			<Form onSubmit={handleSubmit(onSubmit)}>
				<Modal.Header id="example-modal-sizes-title-lg" style={{ paddingTop: '0px' }}>
					<Modal.Title>
						<b>
							[{getStockName(stockInfo?.type)}] {myStockInfo?.name}{' '}
							<span style={getNumberColor(Number(stockInfo?.diffRatio))}>
								{stockInfo?.diffRatio}%
							</span>
						</b>{' '}
						<i
							style={{ cursor: 'pointer' }}
							onClick={() => fetchData(myStockInfo?.name)}
							className={`nc-icon nc-refresh-02 ${isLoadingInfo && 'fa-spin'}`}
						></i>
						<br />
						<small>
							:
							{stockInfo?.dividend ?? 0 * 100}% <br />
							{stockInfo?.comment}
						</small>
					</Modal.Title>
				</Modal.Header>
				<Modal.Body className="show-grid" style={{ paddingTop: '12px' }}>
					<Container>
						<Row>
							<Col xs={6} md={2}>
								<Form.Group className="mb-3">
									<label> / </label>
									<br></br>
									<Form.Select
										style={{ height: 38 }}
										{...register('type', {
											value: 'b',
											onChange: calcTotalMoney,
										})}
									>
										<option value="b"></option>
										<option value="s"></option>
									</Form.Select>
								</Form.Group>
							</Col>
							<Col xs={12} md={3}>
								<Form.Group>
									<label></label>
									<Form.Control
										defaultValue="1"
										type="number"
										isInvalid={errors.cnt}
										{...register('cnt', {
											onChange: calcTotalMoney,
											min: 1,
											valueAsNumber: true,
										})}
									></Form.Control>
									<Form.Control.Feedback type="invalid">
										<p>0     .</p>
									</Form.Control.Feedback>
								</Form.Group>
							</Col>
							<Col xs={6} md={7}>
								<Form.Group>
									<label>/ ()</label>
									<Form.Control
										value={` ${setComma(totalMoney)}`}
										placeholder="/ "
										disabled
										type="text"
									></Form.Control>
								</Form.Group>
							</Col>
						</Row>
					</Container>
					<Table className="table-hover table-striped">
						<thead>
							<tr>
								<th className="border-0">ID</th>
								<th className="border-0"></th>
								<th className="border-0"> </th>
								<th className="border-0"> </th>
								<th className="border-0"> </th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>1</td>
								<td>
									[{getStockName(stockInfo?.type)}]{myStockInfo?.name}
								</td>
								<td ref={myValueRef}>{setComma(myStockInfo?.myValue ?? 0, true)}</td>
								<td>{setComma(stockInfo?.value ?? 0, true)}</td>
								<td ref={myCntRef}>{setComma(myStockInfo?.cnt ?? 0)}</td>
							</tr>
						</tbody>
					</Table>
					<hr />
					<Container>
						<Row>
							<Col className="text-center" xs={12} md={3}>
								{setComma(totalMyMoney, true)} 
							</Col>
							<Col className="text-center" xs={12} md={1}>
								{getValues('type') === 's' ? '+' : '-'}
							</Col>
							<Col className="text-center" xs={12} md={3}>
								{setComma(totalMoney, true)} 
							</Col>
							<Col className="text-center" xs={12} md={1}>
								=
							</Col>
							<Col className="text-center" xs={12} md={4}>
								{setComma(
									totalMyMoney + totalMoney * (getValues('type') === 's' ? 1 : -1),
									true,
								)}{' '}
								
							</Col>
						</Row>
					</Container>
				</Modal.Body>
				<Modal.Footer>
					<Button onClick={onCloseModal}></Button>
					<Button disabled={isLoadingInfo} variant="secondary" type="submit">
						/
					</Button>
				</Modal.Footer>
			</Form>
		</Modal>
	);
}

MyVerticallyCenteredModal.propTypes = {
	onHide: PropTypes.func.isRequired,
	show: PropTypes.bool.isRequired,
	myStockInfo: PropTypes.object.isRequired,
	dataRefresh: PropTypes.func.isRequired,
	totalMyMoney: PropTypes.number.isRequired,
};

export default MyVerticallyCenteredModal;
